/*------ navlist start ------*/
.nav-list {
  width: 100%;
  background-color: #fff;
  padding: 15px 0 0 0;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    position: relative;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    li {
      font-family: ar-md;
      font-size: 14px;
      color: #000;
      text-transform: uppercase;
      margin: 0 0 0 50px;
      transition: all .3s ease-in-out;
      &:hover {
        color: #d6022a;
        .mega-menu {
          top: 100%;
          opacity: 1;
          visibility: visible;
        }
      }
      &:first-child {
        font-family: ar-md;
        font-size: 15px;
        color: #fff;
        background-color: #410f82;
        width: 270px;
        height: 45px;
        margin: 0 0 0 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 13px 13px 0 0;
        position: relative;
        button {
          padding: 0 12px 0 95px;
        }
        span {
          &:nth-of-type(1) {
            font-size: 16px;
          }
          &:nth-of-type(2) {
            font-size: 11px;
            padding: 3px 0 0 0;
          }
        }
      }
    }
  }
  .sub-list {
    position: absolute;
    top: 100%;
    right: 0;
    z-index: 9;
    width: 100%;
    background-color: #fff;
    border-radius: 0 0 13px 13px;
    padding: 10px 25px;
    display: none;
    li {
      font-family: ar-md !important;
      font-size: 14px !important;
      color: #000 !important;
      background-color: #fff !important;
      display: flex !important;
      justify-content: flex-start !important;
      align-items: center !important;
      width: unset !important;
      height: unset !important;
      padding: 10px 0 !important;
      margin: unset !important;
      span {
        color: #757575;
        padding: 0 2px;
        font-size: 14px !important;
      }
      &:hover {
        color: #d6022a !important;
      }
    }
  }
  .buy-options {
    display: flex;
    justify-content: center;
    align-items: center;
    a {
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: ar-md;
      font-size: 15px;
      color: #000;
    }
    button {
      font-size: 30px;
      color: #000;
      position: relative;
      padding: 0 10px 5px 10px;
      &:hover {
        .counter {
          transform: rotateY(360deg);
          background-color: #410f82;
        }
      }
      .counter {
        position: absolute;
        right: 5px;
        bottom: 1px;
        border-radius: 50%;
        background-color: #d6022a;
        color: #fff;
        width: 18px;
        height: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: ar-reg;
        font-size: 12px;
        padding: 1px 0 0 0;
        transition: all .5s ease-in-out;
      }
      span {
        font-family: ar-md;
        font-size: 15px;
        color: #000;
      }
    }
    .menu-icon, .search-icon {
      display: none;
    }
  }
}
/*------ navlist end ------*/

.mega-menu {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 150%;
  left: 50%;
  transform: translateX(-50%);
  width: 100% !important;
  background-color: #fff;
  z-index: 9;
  border-radius: 0 0 13px 13px;
  box-shadow: 0px 1px 9px 0px rgba(2, 2, 2, 0.22),inset 0px 2px 0px 0px rgba(214, 2, 42, 0.004);
  border-top: 2px solid #d6022a;
  padding: 0 0 35px 0;
  transition: all .5s ease-in-out;
  ul {
    padding: 0 !important;
    margin: 0 !important;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    li {
      background-color: #fff !important;
      display: flex !important;
      justify-content: flex-start !important;
      align-items: center !important;
      width: unset !important;
      height: unset !important;
      text-align: center;
      margin: 35px 25px 0 25px !important;
      max-width: 140px !important;
      img {
        transition: all .5s ease-in-out;
      }
      &:hover {
        img {
          opacity: 0.5;
        }
        span {
          color: #d6022a;
        }
      }
    }
    .img-container {
      width: 139px;
      height: 139px;
      border-style: solid;
      border-width: 1px;
      border-color: rgb(218, 218, 218);
      border-radius: 8px;
      background-color: rgb(255, 255, 255);
      display: flex;
      justify-content: center;
      align-items: center;
    }
    span {
      font-family: ar-md;
      font-size: 14px !important;
      color: #000;
      margin: 20px 0 0 0;
      display: block;
      transition: all .3s ease-in-out;
    }
  }
}



/*------ header start ------*/
.header {
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
        .owl-prev, .owl-next {
          outline: none;
          width: 50px;
          height: 50px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          font-size: 14px;
          background-color: rgba(0, 0, 0, 0.5);
          // border-radius: 50%;
          color: rgb(255, 255, 255);
          transition: all .5s ease-in-out;
          &:hover {
            color: #1a1a1a;
            background-color: #fff;
          }
        }
        .owl-prev {
          right: 0px;
        }
        .owl-next {
          left: 0px;
        }
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: 30px;
        transform: translateX(-50%);
        .owl-dot {
          width: 5px;
          height: 5px;
          background-color: #9d9d9d;
          border-radius: 50px;
          outline: none;
          margin: 0 4px;
          transition: all .3s ease-in-out;
          &.active {
            background-color: #410f82;
            width: 25px;
            height: 5px;
          }
        }
      }
    }
   
}

/*------ header end ------*/




