/*--- card start ---*/
.card {
  width: 268px;
  min-height: 314px;
  overflow: hidden;
  position: relative;
  margin: 0 auto;
  padding: 18px 18px;
  border-radius: 10px;
  transition: all .3s ease-in-out;
  &::after {
    content: "";
    width: 1px;
    height: 316px;
    background-color: #d0d0d0;
    position: absolute;
    top: 15px;
    left: 0; 
  }
  
  &:hover {
    &::after {
      display: none;
    }
    box-shadow: 0px 0px 6px 0px rgba(2, 2, 2, 0.24);
    .add-to-cart {
      opacity: 1;
      visibility: visible;
    }
  }
  .card-img {
    height: 212px;
    margin: 0 0 0px 0;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      transition: all .5s ease-in-out;
    }
  }
  .card-body {
    padding: 0 5px;
  }
  .title {
    font-family: ar-bold;
    font-size: 15px;
    color: #410f82;
    margin: 30px 0 5px 0;
    padding: 0 0 5px 0;
  }
  .price-fav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .fav {
      width: 36px;
      height: 36px;
      border-radius: 50%;
      background-color: #d0d0d0;
      font-size: 20px;
      color: #ffffff;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: all .5s ease-in-out;
      &:hover {
        background-color: #d6022a;
      }
    }
  }
  .price {
    font-family: ar-reg;
    font-size: 24px;
    color: #d6022a;
    margin: 0 0 0 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 0 5px 0;
    .old {
      font-family: ar-reg;
      font-size: 15px;
      color: #757575;
      text-decoration: line-through;
      margin: 5px 10px 0 10px;
    }
  }
  .add-to-cart {
    opacity: 0;
    visibility: hidden;
    border-top: 1px solid #e0e0e0;
    margin: 15px 0 0 0;
    padding: 15px 0 0 0;
    transition: all .3s ease-in-out;
    button {
      font-family: ar-reg;
      font-size: 16px;
      color: #9c9c9c;
      text-align: center;
      display: block;
      margin: 0 auto;
      transition: all .3s ease-in-out;
      &:hover {
        color: #d6022a;
      }
      span {
        font-size: 18px;
        padding: 0 5px;
      }
    }
  }
}

/*--- card end ---*/








